$primary-color: white;
$bg: black;
$test-color: #ff7f82;


$hostile-color: #ff000d;
$hostile-color-transparent: rgba(255, 0, 13, 0.25);
$hostile-color-semi-transparent: rgba(255, 0, 13, 0.3);
$friendly-color: #175bff;
$friendly-color-transparent: rgba(23, 91,255, 0.3);
$friendly-color-semi-transparent: rgba(23, 91, 255, 0.4);
$confirm-color: lawngreen;

$dark-highlight-color: orange;
$light-highlight-color: gold;


body {
  background: $bg;
  margin: 0;
  padding: 0;
  color: $primary-color;
  font-family: 'Inter', Arial, Helvetica, sans-serif;
}

canvas {
  display: block;
}

/* =================================================*/
/* General reusable Classes */

.lightColorHighlight {
  color: $light-highlight-color;
}
.darkColorHighlight {
  color: $dark-highlight-color;
}
.friendlyColorHighlight {
  color: $friendly-color;
}
.hostileColorHighlight {
  color: $hostile-color;
}

.transparentBlackBackground {
  background: rgba(0, 0, 0, 0.5);
}


.fullParentWidth {
  width: 100%;
}

.centerContent {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
}

.clickable:hover {
  cursor: pointer;
}

.colorGoldOnHover:hover {
  color: gold!important; /* important necessary to overwrite coloring inside ids*/
}

.notSelectable {
  user-select: none;
  pointer-events: none;
}

.selectable {
  user-select: initial;
  pointer-events: initial;
}

.unavailableButton {
  background: #666!important;
  //color: #666666!important;
  cursor: default!important;
  border: 3px solid #333!important;
  //user-select: none;
}

.borderBox {
  box-sizing: border-box;
}

.hideElement {
  display: none!important;
}


/* =================================================*/



/* =================================================*/
/* Character Frames Styling */


#characterFramesContainer {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  height: 10%;
  width: 30%;
  z-index: 1;

  .characterFrameHpNumber {
    font-size: 2rem;
    width: 48%;
  }

}

.characterFrame {
  min-width: 50%;
  height: 100%;
  margin-right: 5px;
  margin-bottom: 1.5rem;

}

.characterFrameHpBackground {
  //background: #333;
  background: rgba(0, 0, 0, 0.25);
  //border: 1px solid red;
  height: 55px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.characterFrameHpNumber {
  font-size: 1.5rem;
  z-index: 10;
  width: 14.5rem;
  height: 55px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: absolute;
}

.characterFrameHpBar {
  //position: absolute;
  //background: lime;
  //background: linear-gradient(90deg, rgba(255,0,0,1) 0%, rgba(237,170,5,1) 50%, rgba(79,255,0,1) 100%);
  width: 99%;
  height: 48px;
  border: 3px solid #333;
  border-radius: 3px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 2rem;
}

.partyFramesContainer .characterFrameHpBar {
  //height: 10%;
  font-size: 1rem;
}


.friendlyTarget {
  border: 1px solid $friendly-color;
  border-radius: 3px;
  background: $friendly-color-transparent;
  height: 100%;
}

.hostileTarget {
  border: 1px solid $hostile-color;
  border-radius: 3px;
  background: $hostile-color-transparent;
  height: 100%;

}

.characterFrameClassImage {
  width: 128px;
  height: 128px;
  z-index: 1;
  filter: drop-shadow(2px 4px 6px black);
}

.characterFrameImageContainer {
  display: flex;
  justify-content: space-between;
}

.characterFrameAllBuffsContainer {
}

.characterFrameBuffsContainer {
  display: flex;
  flex-flow: wrap;
}

.characterFrameBuffImage {
  height: 50px;
}

.buffHostileAllegiance {
  border: 1px solid $hostile-color;
  border-radius: 3px;
}
.buffFriendlyAllegiance {
  border: 1px solid $friendly-color;
  border-radius: 3px;
}

.innerPartyFrame {
  width: 15rem;
}

.singlePartyFrameContainer {
  display: flex;
  .characterFrameBuffStacks {
    font-size: 1.2rem;
  }
  .characterFrameBuffTimer {
    font-size: 1.2rem;
  }
}


.characterFrameBuffImageContainer {
  position: relative;
  box-sizing: border-box;
  height: 100%;
}

.characterFrameDRImageContainer {
  position: relative;
  box-sizing: border-box;
  height: 50%;
  img {
    filter: grayscale(100%);
  }
}

.characterDRContainer {
  display: flex;
  justify-content: flex-end;
}

.trinketCdDisplay {
  height: 60px;
  display: flex;
  position: relative;
  justify-content: flex-end;
  img {
    border: 1px solid $hostile-color;
    border-right: none; // to avoid borders overlapping with party frame
  }

  .cardCoolDownDisplay {
    font-size: 2.5rem;
    left: initial;

  }
}
#myPartyFramesContainer {
  .trinketCdDisplay {
    justify-content: flex-start;
    img {
      border: 1px solid $friendly-color;
      border-left: none; // to avoid borders overlapping with party frame
    }
  }
  .characterDRContainer {
    justify-content: flex-start;
  }
}

.trinketAndDRContainer {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
}

/*
#enemyPartyFramesContainer {
  .trinketCdDisplay {
    height: 110px;
  }
  .cardCoolDownDisplay {
    left: initial;
    right: 0.7rem;
    top: 1rem;
    font-size: 3.8rem;
  }
}
*/
.trinketOnCd {
  filter: grayscale(100%);
}

.trinketIcon {
  height: 100%;
  border: 1px solid #333;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  box-sizing: border-box;
}


.characterFrameBuffStacks {
  position: absolute;
  right: 0;
  top: 30px;
  font-size: 1.3rem;
  z-index: 10;
  color: $light-highlight-color;
  background: rgba(0,0,0,0.5);
}
.characterFrameBuffTimer {
  position: absolute;
  left: 1px;
  top: 1px;
  font-size: 1.3rem;
  z-index: 10;
  color: $light-highlight-color;
  background: rgba(0,0,0,0.5);
}

.partyFramesBuffContainer {
  .characterFrameBuffImageContainer {
    min-height: 26px;
  }
  .characterFrameBuffStacks {
    font-size: 1rem;
  }
  .characterFrameBuffTimer {
    font-size: 1rem;
  }
  img {
    width: 30px;
    height: 30px;
  }
}



#targetFrameCastbar {
  display: flex;
  height: 45px;
}

#playerFrameCastbar {
  display: flex;
  height: 45px;
}


.activeButton {
  color: white;
  border: 3px solid $dark-highlight-color;
  pointer-events: initial;
  user-select: initial;

  &:hover {
    background: white!important;
    color: #333;
    border: 3px solid #333;
  }
  transition: 0.2s;
}

.transparentBlack {
  background: rgba(0, 0, 0, 0.5);
}
.transparentGold {
  background: rgba(255, 215, 0, 0.5);
}

.transparentRed {
  background: rgba(109, 0, 0, 0.75);
}


// Party Frames ------------------------------------------

.partyFramesContainer {
  .characterFrameClassImage {
    height: 76px;
    width: 76px;
  }

  .characterFrameBuffImageContainer {
    .characterFrameBuffStacks {
      top: 13px;
    }
  }
  //width: 20%;
}

#myPartyFramesContainer {
  position: absolute;
  left: 0;
  top: 25%;

}

#enemyPartyFramesContainer {
  position: absolute;
  right: 0;
  top: 25%;
  // necessary to align the character frames on the right side of the container
  display: flex;
  flex-flow: column;
  align-items: flex-end;

  .characterFrameHpNumber {
    //width: 65%;
  }
}

.primaryActiveHeroFrame {
  //border: 3px solid gold!important;
  box-sizing: border-box;
}

.primaryActiveFriendlyHeroFrame {
  background: $friendly-color-semi-transparent;
  border: 3px solid $dark-highlight-color !important;
  box-sizing: border-box;
  filter: drop-shadow(0px 0px 8px $friendly-color) drop-shadow(0px 0px 2px #6e73ff) drop-shadow(0px 0px 2px gold);
  /*
  .characterFrameClassImage {
    filter: drop-shadow(2px 4px 8px $friendly-color) drop-shadow(2px 2px 2px red);
  }*/
}

.primaryActiveHostileHeroFrame {
  background: $hostile-color-semi-transparent;
  border: 3px solid $dark-highlight-color !important;
  filter: drop-shadow(0px 0px 8px $dark-highlight-color) drop-shadow(0px 0px 2px #950000);
  box-sizing: border-box;
}

.myPartyCastbar {
  border: 1px solid $friendly-color-transparent;
  height: 25px;
  display: flex;
  align-items: center;
  font-size: 0.8rem;
  background: rgba(0, 0, 0, 0.25);
}
.enemyPartyCastbar {
  border: 1px solid $hostile-color-transparent;
  height: 25px;
  display: flex;
  align-items: center;
  font-size: 0.8rem;
  background: rgba(0, 0, 0, 0.25);
}

.partyFrameGlobalContainer {
  display: flex;
  padding-bottom: 1px;
  &>div {
    font-size: 1rem!important;
  }
}

.switchHeroButton {
  //width: 50%;
  //width: 5rem;
  //height: 5rem;
  //width: 73.5%;
  //width: 75%;
  width: 15rem;
  border: 1px solid $dark-highlight-color;
  box-sizing: border-box;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  &:hover {
    cursor: pointer;
    color: #333;
    background: #fff;
    border: 1px solid #333;
  }
  transition: 0.2s;
}

.unavailableSwitchHeroButton {
  //width: 75%;
  padding: 10px;
  display: flex;
  box-sizing: border-box;
  width: 15rem;

  justify-content: center;
  align-items: center;
  background: #666!important;
  color: white;
  cursor: default!important;
  border: 1px solid #333!important;
  border-radius: 3px;

  &:hover {
    //color: #666666!important;
  }
}

/* =================================================*/

/* Specific Buttons */

#endTurnButton {
  width: 170px;
  height: 4rem;
  margin: 30px auto 0;
  border-radius: 3px;
  font-size: 1.2rem;
  background: rgba(0, 0, 0, 0.2);
}

.turnOverButtonStyle {
  background: white!important;
  color: #333!important;
  border: 3px solid #333!important;
}

#moveHeroButton {
  width: 8rem;
  height: 8rem;
  font-size: 1.2rem;
  border-radius: 100%;
  position: absolute;
  right: 20%;
  bottom : 30%;
  background: rgba(0, 0, 0, 0.2);
}

/* =================================================*/

/* Specific Page Elements */

#gameContainer {
  height: 100vh;
  position: relative;
  overflow: hidden; // at some point fix that there will never be overflow
}

#canvasContainer {
  canvas {
    pointer-events: auto;
  }
  height: 100%;
}

#playerHandContainer {
  border-top: 3px solid #333;
  width: 100%;
  position: absolute;
  bottom: 0;
  background: rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  height: 15%;
}

#activeHeroHandContainer {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
#passiveHeroesHandContainer {
  width: 20%;
  height: 100%;
  font-size: 0.5rem;
  border-right: 3px solid #333;
  .cardCoolDownDisplay {
   font-size: 1.5rem;
  }
}
#passiveHeroHandContainer1 {
  height: 50%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  &>div {
    height: 27%;
  }
}
#passiveHeroHandContainer2 {
  height: 50%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  &>div {
    height: 27%;
  }
}


#globalInfoContainer {
  position: absolute;
  bottom: 23%;
  left: 40%;
  width: 20%;
}

#castbarContainer {
  border: 1px solid #333;
  margin-bottom: 9px;
  border-radius: 3px;
  display: flex;
  height: 30px;
  font-size: 1.2rem;
}

.castbarSegment {
  border: 1px solid #333;
  border-radius: 3px;
  background: rgba(191, 16, 255, .9);
  display: flex;
  height: 100%;
  align-items: center;
}

#globalFramesContainer {
  display: flex;
  justify-content: space-between;
}

.globalFrame {
  border: 1px solid brown;
  border-radius: 10px;
  width: 25%;
}


#confirmCastContainer {
  height: 4rem;
  font-size: 2rem;
  margin-bottom: 10px;

}

#continueCastButton {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 50%;
  background: rgba(0, 0, 0, 0.2);
  border: 2px solid black;
  border-right: 1px solid black;
  color: $confirm-color;
  &:hover {
    background: white;
    color: #333333;
  }
}

#stopCastButton {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 50%;
  background: rgba(0, 0, 0, 0.2);
  color: $hostile-color;
  border: 2px solid black;
  border-left: 1px solid black;
  &:hover {
    background: white;
    color: #333333;
  }
}

#fakeCastMessage {
  color: $dark-highlight-color;
  padding: 5px;
  margin-bottom: 9px;
}


#preQueuedGlobalsContainer {
  border: 1px solid blueviolet;
}


#activeHeroCCDisplay {
  user-select: none;
  position: absolute;
  width: 100%;
  top: 5px;
  //border: 1px solid purple;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;

  .characterFrameBuffImage {
    height: 100px;
  }
}

#activeHeroCCIcons {
  display: flex;
  justify-content: center;
  align-items: center
}

#activeHeroCCText {
  font-size: 2rem;
  color: $dark-highlight-color;
}

/* =================================================*/
/* Card Styling */

.cardStyle {
  background: black;
  //height: 56%;
  height: 120px;
  //max-width: 150px;
  width: 120px;
  //width: 15%;
  border: 3px solid #333;
  //overflow: hidden;
  position: relative;
  border-radius: 3px;
  &:hover {
    box-shadow: 0 5px 110px 25px red, 1px 6px 22px 7px rgba(255, 255, 0, 1);
  }
}





// get shadow styles from old client
.cardIsPlayable {
  //box-shadow: 0 0 30px 10px #2eb7ff; // blue shadow
  //box-shadow: 0 0 29px 6px gold; // smaller shadow
  //box-shadow: 0 0 10px -1px gold; // smallest shadow
  box-shadow: 0 0 19px -1px red, 0 0 4px 1px yellow;
}

.cardNeedsProcHighlighting {
  box-shadow: 0 0 30px 10px gold; // BIG gold shadow <- will be used for procs!
}

.cardIsUnplayable {
  border: 1px solid #666666;
  user-select: none!important;
  cursor: default!important;
  img {
    filter: grayscale(100%);
  }
}

.cardImage {
  width: 100%;
  height: auto;
}

.cardTooltip {
  border: 3px solid #333;
  border-radius: 3px;
  position: absolute;
  top: -300px;
  left: -100px;
  font-size: 1rem;
  background: rgba(0, 0, 0, 0.75);
  color: $dark-highlight-color;
  width: 300px;
  z-index: 1;
  text-shadow: 1px 1px black;

  &>div {
    //border-bottom: 1px solid #333;
    padding: 5px;
    &>span {
      color: white;
    }
  }
}

.cardCoolDownDisplay {
  position: absolute;
  top: 10%;
  left: 10%;
  color: $dark-highlight-color;
  font-size: 5rem;
  z-index: 10;
  text-shadow: 1px 1px rgba(0, 0, 0, 0.5);
}

.cardKeybindDisplay {
  position: absolute;
  top: 1px;
  right: 5px;
  color: $dark-highlight-color;
  font-size: 1rem;
  z-index: 10;
  //background: rgba(0, 0, 0, 0.5);
  text-shadow: 1px 1px black;
}

.cardTypeDisplay {
  position: absolute;
  top: 0;
  left: 0;
  //color: white;
  color: $dark-highlight-color;
  font-size: 1.5rem;
  z-index: 10;
  display: flex;
  flex-flow: column;
  height: 100%;
  width: 100%;
  justify-content: space-between;
  align-items: flex-end;
  text-shadow: 0 0 3px black;

  &:hover {
   //background: rgba(0, 0, 0, 0.25);
    //filter: drop-shadow(0 0 12px black);
    //filter: drop-shadow(0 0 12px black);
  }
}

.cardTypeAndCostInfo {
  display: flex;
  justify-content: space-between;
  width: 100%;
  font-size: 1.5rem;
}

.cardNameStyle {
  //padding-top: 0.5rem;
  font-size: 1rem;
  //background: rgba(0, 0, 0, 0.5);
}

.cardInfoIconStyle {
  font-size: 1.5rem;
  margin-top: -0.2rem;
}





/* =================================================*/
// Animation Elements

.floatingCombatText {
  font-size: 0;
  transition: opacity 2s ease-out, font-size .1s linear;
  position: absolute;
}

.myDamage {
  color: $dark-highlight-color;
  font-size: 4rem;
}

.heal {
  color: $confirm-color;
  font-size: 4rem;
}

.enemyDamage {
  color: #910000;
  font-size: 3rem;
}

.myHPMIcon {
  height: 4rem;
}

.enemyHPMIcon {
  height: 3rem;
}

.fadeOut {
  opacity: 0;
}


.highlightMessage {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  font-size: 2rem;
  color: $light-highlight-color;
  //transition: opacity 2s ease-out, font-size .1s linear;
}

.highlightMessageFriendlyHeroImage {
  height: 4rem;
  background: $friendly-color-semi-transparent;
}
.highlightMessageHostileHeroImage {
  height: 4rem;
  background: $hostile-color-semi-transparent;
}
.highlightMessageCardImage {
  height: 3rem;
}


#messageContainer {
  position: absolute;
  width: 100%;
  top: 30%;
}


/* =================================================*/
/* History */

#roundNumberDisplay {
  position: absolute;
  top: 11%;
  right: 0;
  width: 20%;
  box-sizing: border-box;
  background: rgba(0, 0, 0, 0.5);
  padding: 5px;
  border: 1px solid #333;
  border-radius: 3px;
  font-size: 1.3rem;
}

#historyContainer {
  z-index: 1;
  position: absolute;
  border: 1px solid #333;
  top: 0;
  right: 0;
  box-sizing: border-box;
  width: 20%;
  height: 10%;
  overflow-y: scroll;
  background: rgba(0, 0, 0, 0.5);

  // adjustments for highlight message copies inside the container
  .highlightMessage {
    justify-content: space-between;
    position: initial;
    font-size: 1rem;
    border-bottom: 1px solid #333;
    border-top: 1px solid #333;
    //background: rgba(0, 0, 0, 0.5);
    box-sizing: border-box;
  }

  .highlightMessageHostileHeroImage, .highlightMessageFriendlyHeroImage {
    height: 2.5rem;
  }
  .highlightMessageCardImage {
    height: 2rem;
  }
}

.historyEntryHeroImage {
  width: 10%;
}


.historyEntry {
  height: 40%;
  border-bottom: 1px solid grey;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  padding: 5px;
}


// ==============================================================

// end of game elements


#gameResultDialogue {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  z-index: 100;
  background: rgba(0, 0, 0, 0.25);
  padding-top: 21%;
  box-sizing: border-box;
}

#gameResultMessage {
  font-size: 10rem;
  margin-bottom: 1rem;
}

#playAgainButton {
  width: 20%;
  padding: 1%;
  margin: auto;
}

// confetti for the end of the game
#confettiContainer {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  z-index: 101;
  pointer-events: none;
}


// ==============================================================

// pre Game Elements

#preGameContainer {
  width: 100vw;
  height: 100vh;
  background: rgb(50,123,201);
  background: radial-gradient(circle, rgba(50,123,201,1) 0%, rgba(83,15,195,1) 56%, rgba(0,6,56,1) 95%);
  position: absolute;
  z-index: 10;
  display: none;
}











